import React, { useState } from "react";

const Todo = () => {
  const [inputVal, setInputVal] = useState("");
  const [data, setData] = useState([]);
  const [toggle, setToggle] = useState(true);
  const [toggleRemove, setToggleRemove] = useState(false);
  const [btnVal, setBtnVal] = useState("Check All");
  const [iseditItem, setIsEditItem] = useState(null);
  const addItem = () => {
    if (!inputVal) {
      alert("Please Enter a Value");
    } else if (inputVal && !toggle) {
      setToggle(true);
      setData(
        data.map((item) => {
          if (item.id === iseditItem) {
            return { ...item, name: inputVal };
          }
          return item;
        })
      );
      setInputVal("");
    } else {
      const allData = { id: new Date().getTime().toString(), name: inputVal };
      setData((prevVal) => {
        return [...prevVal, allData];
      });
      setInputVal("");
    }
  };

  const editItem = (id) => {
    const selectedItem = data.find((item) => {
      return item.id === id;
    });
    console.log(typeof selectedItem.name);
    setInputVal(selectedItem.name);
    setToggle(false);
    setIsEditItem(id);
  };

  const delItem = (id) => {
    const updatedItem = data.filter((item) => {
      return id !== item.id;
    });
    setData(updatedItem);
  };

  const delAll = () => {
    setData([]);
  };
  return (
    <>
      <div className="container mx-auto		mt-10 flex justify-center font-mono	items-center 	">
        <div className="container h-96	w-80  flex justify-start items-center 	flex-col	">
          <div className="mt-10">
            <svg
              xmlns="http://www.w3.org/2000/svg"
              className="h-14 w-22 text-yellow-400	 "
              viewBox="0 0 20 20"
              fill="currentColor"
            >
              <path d="M8 3a1 1 0 011-1h2a1 1 0 110 2H9a1 1 0 01-1-1z" />
              <path d="M6 3a2 2 0 00-2 2v11a2 2 0 002 2h8a2 2 0 002-2V5a2 2 0 00-2-2 3 3 0 01-3 3H9a3 3 0 01-3-3z" />
            </svg>
          </div>
          <h1 className="mt-4">Add your task here</h1>
          <div className="container mt-4 shadow appearance-none border rounded leading-tight focus:outline-none focus:shadow-outline py-2 pl-6  pr-2 flex bg-white justify-between w-10/12">
            <input
              className="   text-gray-700 bg-transparent outline-none"
              placeholder="Add item..."
              value={inputVal}
              onChange={(e) => setInputVal(e.target.value)}
            />
            {toggle ? (
              <svg
                xmlns="http://www.w3.org/2000/svg"
                className="h-5 w-5 text-gray-700 font-bold cursor-pointer"
                viewBox="0 0 20 20"
                fill="currentColor"
                onClick={addItem}
              >
                <path
                  fillRule="evenodd"
                  d="M10 3a1 1 0 011 1v5h5a1 1 0 110 2h-5v5a1 1 0 11-2 0v-5H4a1 1 0 110-2h5V4a1 1 0 011-1z"
                  clipRule="evenodd"
                />
              </svg>
            ) : (
              <svg
                xmlns="http://www.w3.org/2000/svg"
                className="h-6 w-6 text-gray-700 font-bold cursor-pointer"
                fill="none"
                viewBox="0 0 24 24"
                stroke="currentColor"
                onClick={addItem}
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth={2}
                  d="M15.232 5.232l3.536 3.536m-2.036-5.036a2.5 2.5 0 113.536 3.536L6.5 21.036H3v-3.572L16.732 3.732z"
                />
              </svg>
            )}
          </div>
          {/*              list container             */}
          <div className="container w-10/12 mt-4">
            {/*              List Items                 */}
            {data.map((item) => {
              return (
                <div
                  className="shadow border rounded leading-tight focus:outline-none focus:shadow-outline py-2 pl-6  pr-2 flex bg-indigo-600 justify-between mt-2.5 "
                  key={item.id}
                >
                  <h3 className="text-white">{item.name}</h3>
                  <div className="flex flex-row w-14 justify-between">
                    {/* Edit Item */}
                    <svg
                      xmlns="http://www.w3.org/2000/svg"
                      className="h-6 w-6 text-white cursor-pointer"
                      fill="none"
                      viewBox="0 0 24 24"
                      stroke="currentColor"
                      onClick={() => editItem(item.id)}
                    >
                      <path
                        strokeLinecap="round"
                        strokeLinejoin="round"
                        strokeWidth={2}
                        d="M15.232 5.232l3.536 3.536m-2.036-5.036a2.5 2.5 0 113.536 3.536L6.5 21.036H3v-3.572L16.732 3.732z"
                      />
                    </svg>

                    {/* Delete Item  */}
                    <svg
                      xmlns="http://www.w3.org/2000/svg"
                      className="h-6 w-6 text-white cursor-pointer "
                      fill="none"
                      viewBox="0 0 24 24"
                      stroke="currentColor"
                      onClick={() => delItem(item.id)}
                    >
                      <path
                        strokeLinecap="round"
                        strokeLinejoin="round"
                        strokeWidth={2}
                        d="M19 7l-.867 12.142A2 2 0 0116.138 21H7.862a2 2 0 01-1.995-1.858L5 7m5 4v6m4-6v6m1-10V4a1 1 0 00-1-1h-4a1 1 0 00-1 1v3M4 7h16"
                      />
                    </svg>
                  </div>
                </div>
              );
            })}

            <div className=" flex items-center	 justify-center mt-4">
              <button
                className="bg-indigo-600 shadow border rounded px-3.5 py-2.5 leading-tight focus:outline-none focus:shadow-outline text-white font-bold"
                onClick={delAll}
                onMouseEnter={() => {
                  setToggleRemove(true);
                  setBtnVal("Remove All");
                }}
                onMouseLeave={() => {
                  setToggleRemove(false);
                  setBtnVal("Check All");
                }}
              >
                {btnVal}
              </button>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default Todo;
